<template>
  <div class="banner-box">
    <swiper :options="bannerOption" class="banner-b" ref="banner" v-if="banner.length>0">
      <!-- slides -->
      <swiper-slide class="banner-item" v-for="(item,index) in banner" :key="index">
        <a :href="item.url" class="item-link">
          <img :src="item.image">
        </a>
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
    <!--<section class="banner-content">-->
      <!--<h1 class="wow slideInDown" data-wow-delay="1s">SHARING TIME</h1>-->
      <!--<p class="wow slideInDown one-line" data-wow-delay="1s">移动互联网创业共享平台</p>-->
      <!--<p class="wow slideInDown pc-word" data-wow-delay="1s">致力于个人创业者资源整合与价值变现 为互联网创造更大规模人口红利</p>-->
      <!--<section class="btn">-->
        <!--<a class="wow slideInLeft" data-wow-delay="1s">了解更多</a>-->
        <!--<a class="wow slideInRight" data-wow-delay="1s">关于我们</a>-->
      <!--</section>-->
    <!--</section>-->
  </div>
</template>

<script>
  import {swiper, swiperSlide} from 'vue-awesome-swiper'


  export default {
    props: {
      banner:{
        type: Array,
        default: []
      }
    },
    computed: {
      swiper() {
        return this.$refs.banner.swiper
      }
    },
    mounted() {

    },
    data() {
      return {
        bannerOption: {
          pagination: {
            el: '.swiper-pagination'
          },
          loop: true,
          observer:true,
          autoplay: {
            disableOnInteraction: false
          },
          ppreventLinksPropagation : false
        }
      }
    },
    components: {
      swiper,
      swiperSlide
    }
  }
</script>
<style rel="stylesheet/scss" lang="scss">
  @import '~common/css/base.scss';
  @import 'swiper/dist/css/swiper.css';

  @media screen and (min-width: 769px) {
    .banner-box{
      position: relative;
      height: 650px;
      .banner-b {
        width: 100%;
        height: 650px;
        overflow: hidden;
        .banner-item{
          width:100%;
          height: 100%;
          .item-link{
            display: block;
            width: 100%;
            height: 100%;
            img{
              width: 100%;
              height: 100%;
            }
          }
        }
      }
      .swiper-pagination{
        position: absolute;
        bottom: 10px;
        left: 50%;
        transform: translateX(-50%);
        span + span{
          margin-left: 5px;
        }
      }
      .banner-content{
        position: absolute;
        z-index: 2003;
        left: 50%;
        top: 25%;
        width: 546px;
        margin-left: -278px;
        text-align: center;
        h1{
          color: #fff;
          font-size: 48px;
          margin-bottom: 10px;
        }
        .one-line{
          font-size: 24px;
        }
        p{
          color: #fff;
          line-height: 36px;
        }
        .btn {
          width: 440px;
          margin: 60px auto 0 auto;
          a:first-child{
            margin-right: 30px;
            background: #fff;
            border: none;
            color: #666;
          }
          a{
            cursor: pointer;
            display: inline-block;
            background: transparent;
            border: 1px solid #fff;
            width: 200px;
            height: 50px;
            color: #fff;
            text-align: center;
            line-height: 50px;
          }
        }
      }
    }
  }
  @media screen and (max-width: 768px) {
    .banner-box {
      position: relative;
      height:156px;
      .banner-b {
        width: 100%;
        height: 156px;
        overflow: hidden;
        .banner-item {
          width: 100%;
          height: 100%;
          .item-link {
            display: block;
            width: 100%;
            height: 100%;
            img {
              width: 100%;
              height: 100%;
            }
          }
        }
      }
      .swiper-pagination{
        position: absolute;
        bottom: 10px;
        left: 50%;
        transform: translateX(-50%);
        span + span{
          margin-left: 5px;
        }
      }
      .banner-content{
        position: absolute;
        z-index: 2003;
        left: 50%;
        top: 45%;
        width:250px;
        margin-left: -125px;
        text-align: center;
        h1{
          color: #fff;
          font-size: 20px;
          margin-bottom: 10px;
        }
        .one-line{
          font-size: 14px;
        }
        p{
          color: #fff;
          line-height: 18px;
        }
        .pc-word {
          display: none;
        }
        .btn {
          width: 200px;
          justify-content: space-around;
          margin: 15px auto 0;
          display: flex;
          a:first-child{
            margin-right: 30px;
            background: #fff;
            border: none;
            color: #666;
          }
          a{
            cursor: pointer;
            display: inline-block;
            background: transparent;
            border: 1px solid #fff;
            width: 88px;
            height: 36px;
            color: #fff;
            text-align: center;
            box-sizing: border-box;
            line-height: 36px;
          }
        }
      }
    }
  }
</style>





