<template>
  <div class="footer" id="footer-content">
    <div class="footer-container">
      <div class="contact">
        <p>地址：{{contactList.address}}</p>
        <p>邮箱：{{contactList.email}}</p>
        <p>电话：{{contactList.tel}}</p>
      </div>
      <div class="QR-code">
        <a><img src="./images/QR_st.jpg" alt=""></a>
        <a><img src="./images/QR.jpg" alt=""></a>
      </div>
      <div class="footer-tip">
        <div class="links">
          <span>友情链接：</span>
          <ul>
            <li v-for="item in links">
              <a :href="item.url">{{item.title}}</a>
            </li>
          </ul>
        </div>
        <div class="links">
          <span>合作伙伴：</span>
          <ul>
            <li v-for="item in partners">
              <a :href="item.url">{{item.title}}</a>
            </li>
          </ul>
        </div>
      </div>
      <div class="company-info">
        <span>©2009 - 2018 佰昌传媒版权所有</span> <span>ICP证：闽ICP备0000000号</span> 技术支持：<a href="https://www.fjcjkj.com">福建创极科技</a>
      </div>
    </div>

  </div>
</template>

<script>
  import 'particles.js'
  import {particlesJson} from 'api/particles'
  import {axiosGet} from "../../api/config"

  export default {
    data() {
      return {
        contactList: {},
        links: [],
        partners: []
      }
    },
    created() {
      let _this = this
      axiosGet('/about').then(res => {
        if(res.status === 200) {
          this.contactList = res.data[0]
        }
      })
      axiosGet('/foots').then(res => {
        if(res.status === 200) {
          res.data.map(item => {
            if(item.type == 0) {
              _this.links.push(item)
            } else {
              _this.partners.push(item)
            }
          })
        }
      })
    },
    mounted() {
      particlesJS('footer-content', particlesJson)
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  @import '~common/css/base.scss';

  @media screen and (min-width: 769px) {
    .footer {
      padding-top: 60px;
      height: 600px;
      width: 100%;
      position: relative;
      background: #fafafd;
      .footer-container{
        @include width-content;
        left: 50%;
        margin-left: -660px;
        position: absolute;
        color: #666;
        .contact{
          display: table;
          margin: 0 auto;
          p{
            line-height: 36px;
          }
        }
        .QR-code {
          display: table;
          margin: 40px auto 0;
          a{
            display: inline-block;
            width: 180px;
            height: 180px;
            border: 1px solid #eee;
            box-sizing: border-box;
            img{
              width: 100%;
              height: 100%;
            }
          }
          a:first-child {
            margin-right: 60px;
          }
        }
        .footer-tip {
          display: table;
          margin:40px auto 0;
          .links{
            margin-bottom: 10px;
            span{
              display: inline-block;
              margin-right: 10px;
            }
            ul {
              display: inline-block;
              li{
                display: inline-block;
                margin-right: 15px;
                line-height: 24px;
              }
            }
          }
        }
        .company-info {
          margin-top: 100px;
          text-align: center;
          color: #666;
        }
      }
    }
  }

  @media screen and (max-width: 768px) {
    .footer {
      height: auto;
      width: 100%;
      position: relative;
      background: #fafafd;
      padding: 30px 0 10px 0;
      font-size: 14px;
      .footer-container {
        text-align: center;
        padding: 0 15px;
        height: auto;
        .contact {
          p{
            line-height: 24px;
          }
        }
        .QR-code {
          display: flex;
          margin-top: 20px;
          justify-content: space-around;
          a{
            flex: 0 0 40%;
            width: 40%;
            height:40%;
            img{
              width: 100%;
              height:100%;
            }
          }
        }
        .footer-tip{
          .links{
            line-height: 24px;
            span{
              display: inline-block;
            }
            ul{
              display: inline-block;
              li{
                display: inline-block;
                margin-right: 5px;
              }
            }
          }
        }
        .company-info{
          margin-top: 10px;
          color: #999;
          line-height: 20px;
          span{
            display: block;
          }
        }
      }
      .particles-js-canvas-el {
        position: absolute;
        bottom: 0;
      }
    }
  }
</style>



