import goodStorage from 'good-storage'

export const getData = key => {
  let tempStorage = goodStorage.session.get(key)
  return new Promise((resolve, reject) => {
    if(!tempStorage) {
      reject(tempStorage)
    } else {
      resolve(tempStorage)
    }
  })
}

export const savaData = (key, val) => {
  goodStorage.session.set(key, val)
}

export const rmData = key => {
  goodStorage.session.remove(key)
}

export const clearData = () => {
  goodStorage.session.clear()
}



