import axios from 'axios'

const baseURL = 'http://www.baichangst.com'
// const baseURL = 'http://qys.cj-api.com'

let config = {
  method: 'get',
  baseURL,
  timeout: 10000,
  params: {}
}
let postConfig = {
  method: 'post',
  baseURL,
  timeout: 10000,
  params: {}
}

export const axiosGet = (url, params) => {
  return axios( { ...config, url, ...params})
}

export const axiosPost = (url, params) => {
  return axios( { ...postConfig, url, ...params})
}



