<template>
  <div class="join" @click="hide">
    <div class="pc-banner">
      <img src="./images/banner.png" alt="">
    </div>
    <div class="wap-banner">
      <img src="./images/mini-banner.png" alt="">
    </div>
    <div class="join-table">
      <h1>在线<span>加盟</span></h1>
      <div class="form-table">
        <confirm-time class="confirm-time" :msg="msg" ref="confirmTime"></confirm-time>
        <div class="line">
          <input type="text" placeholder="个人姓名" v-model="nameVal">
          <div class="sex" @click.stop="showFunc">
            <p>{{sexName}}</p>
          </div>
          <ul class="join-ul" v-show="showFlag">
            <li v-for="(item, index) in sexList" @click="chooseSex(item, index)">
              {{item}}
            </li>
          </ul>
        </div>
        <div class="line line-group">
          <input type="text" placeholder="电话" v-model="telVal">
        </div>
        <div class="line line-group">
          <div class="sex" @click.stop="showFunc1">
            <p>{{groupName}}</p>
          </div>
          <ul class="join-ul" v-show="showFlag1">
            <li v-for="(item, index) in groupList" @click="chooseGroup(item, index)">
              {{item}}
            </li>
          </ul>
        </div>
        <div class="line line-group">
          <div class="province" @click.stop="showPro">
            <p>{{proName}}</p>
            <ul class="pro-ul" v-show="showFlag2">
              <li v-for="item in proList" @click.stop="choosePro(item.address_name, item.address_id)" :data-id="item.address_id">
                {{item.address_name}}
              </li>
            </ul>
          </div>
        </div>
        <div class="line line-group">
          <div class="city" @click.stop="showCity">
            <p>{{cityName}}</p>
            <ul class="pro-ul" v-show="showFlag3">
              <li v-for="item in cityList" @click.stop="chooseCity(item.address_name, item.address_id)" :data-id="item.address_id">
                {{item.address_name}}
              </li>

            </ul>
          </div>
        </div>
        <div class="line line-group">
          <div class="province" @click.stop="showCounty">
            <p>{{countyName}}</p>
            <ul class="pro-ul" v-show="showFlag4">
              <li v-for="item in countyList" @click.stop="chooseCounty(item.address_name, item.address_id)" :data-id="item.address_id">
                {{item.address_name}}
              </li>
            </ul>
          </div>
        </div>
        <div class="line line-group">
          <input type="text" placeholder="详细地址" v-model="addVal">
        </div>
        <div class="btn" @click="postSubmit">
          提交
        </div>
      </div>

    </div>
    <b-footer></b-footer>

  </div>
</template>

<script>
  import BFooter from 'base/footer/footer'
  import ConfirmTime from 'base/confirmTime/confirmTime'
  import {axiosGet, axiosPost} from "../../api/config"

  export default {
    data() {
      return {
        sexList: ['先生', '女士'],
        sexName: '先生',
        groupList: ['个人', '代理商'],
        groupName: '个人',
        showFlag: false,
        showFlag1: false,
        showFlag2: false,
        showFlag3: false,
        showFlag4: false,
        proList: [],
        proName: '省',
        cityList: [],
        cityName: '市',
        countyList: [],
        countyName: '县',
        postId: '',
        sexId: '',
        groupId: '',
        nameVal: '',
        telVal: '',
        addVal: '',
        msg: ''
      }
    },
    created() {
      axiosGet('/address/pro').then(res => {
        if(res.status === 200) {
          this.proList = res.data
        }
      })
    },
    methods: {
      choosePro(name, id) {
        this.showFlag2 = false
        this.proName = name
        this.cityName = '市'
        this.countyName = '县'
        this.cityList = []
        this.countyList = []
        axiosGet(`/addresss/${id}`).then(res => {
          if(res.status === 200) {
            this.cityList = res.data
          }
        })
      },
      chooseCity(name, id) {
        this.showFlag3 = false
        this.cityName = name
        this.countyName = '县'
        this.countyList = []
        axiosGet(`/addresss/${id}`).then(res => {
          if(res.status === 200) {
            this.countyList = res.data
          }
        })
      },
      chooseCounty(name, id) {
        this.showFlag4 = false
        this.countyName = name
        this.postId = id
      },
      chooseSex(sex, id) {
        this.sexName = sex
        this.showFlag = false
        if(id == 1) {
          this.sexId = '女'
        } else {
          this.sexId = '男'
        }

      },
      chooseGroup(group, id) {
        this.groupName = group
        this.showFlag1 = false
        if(id == 1) {
          this.groupId = '2'
        } else {
          this.groupId = '1'
        }
      },
      postSubmit() {
        this.sexId = !this.sexId ? '男' : this.sexId
        this.groupId = !this.groupId ? '1' : this.groupId
        let detailName = `${this.proName}${this.cityName}${this.countyName}${this.addVal}`
        if(!this.nameVal) {
          this.msg = '请输入正确的姓名'
          this.$refs.confirmTime.show()
          return
        }
        if(!this.telVal) {
          this.msg = '请输入正确的电话'
          this.$refs.confirmTime.show()
          return
        }
        if(!this.postId) {
          this.msg = '请输入正确的省市县'
          this.$refs.confirmTime.show()
          return
        }
        if(!this.addVal) {
          this.msg = '请输入正确的详细地址'
          this.$refs.confirmTime.show()
          return
        }
        axiosPost('/join', {
          params: {
            address: detailName,
            sex: this.sexId,
            type: this.groupId,
            tel: this.telVal,
            name: this.nameVal
          }
        }).then(res => {
          if(res.status === 200) {
            if(res.data.state == 1) {
              this.msg = res.data.msg
              this.nameVal = ''
              this.telVal = ''
              this.addVal = ''
              this.proName = '省'
              this.cityName = '市'
              this.countyName = '县'
              this.sexId = ''
              this.groupId = ''
              this.sexName = '先生'
              this.groupName = '个人'
            } else {
              this.msg = `${res.data.msg}，请检查信息是否填写正确！`
            }
            this.$refs.confirmTime.show()
          }
        })
      },
      hide() {
        this.showFlag = false
        this.showFlag1 = false
        this.showFlag2= false
        this.showFlag3 = false
        this.showFlag4 = false
      },
      showPro() {
        this.showFlag2 = !this.showFlag2
        this.showFlag = false
        this.showFlag1= false
        this.showFlag3 = false
        this.showFlag4 = false
      },
      showCity() {
        this.showFlag3 = !this.showFlag3
        this.showFlag1 = false
        this.showFlag2= false
        this.showFlag = false
        this.showFlag4 = false
      },
      showCounty() {
        this.showFlag4 = !this.showFlag4
        this.showFlag1 = false
        this.showFlag2= false
        this.showFlag3 = false
        this.showFlag = false
      },
      showFunc() {
        this.showFlag = !this.showFlag
        this.showFlag1 = false
        this.showFlag2= false
        this.showFlag3 = false
        this.showFlag4 = false
      },
      showFunc1() {
        this.showFlag1 = !this.showFlag1
        this.showFlag = false
        this.showFlag2= false
        this.showFlag3 = false
        this.showFlag4 = false
      }
    },
    components: {
      BFooter,
      ConfirmTime
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  @import '~common/css/base.scss';

  @media screen and (min-width: 769px) {
    .join{
      @include wrapper();
      .pc-banner {
        position: relative;
        height: 650px;
        overflow: hidden;
        img {
          width: 100%;
          height: auto;
        }
      }
      .wap-banner {
        display: none;
      }
      .join-table{
        padding-bottom: 100px;
        h1 {
          font-size: 40px;
          height: 150px;
          line-height: 150px;
          text-align: center;
          span{
            color: #23c2bd;
          }
        }
        .form-table{
          width: 880px;
          margin: 0 auto;
          padding: 40px;
          border-radius: 10px;
          -webkit-box-shadow: 5px 5px 20px #ddd;
          -moz-box-shadow: 5px 5px 20px #ddd;
          box-shadow: 5px 5px 20px #ddd;
          position: relative;
          .confirm-time {
            position: absolute;
            left: 50%;
            top: 50%;
            margin-left: -100px;
            margin-top: -25px;
          }
          .line{
            display: flex;
            justify-content: space-around;
            position: relative;
            margin-bottom: 30px;
            input {
              flex: 1 1 540px;
              width: 540px;
              height: 64px;
              padding: 0 15px;
              box-sizing: border-box;
              border: 1px solid #23c2bd;
              margin-right: 15px;
            }
            .sex {
              flex: 0 0 240px;
              width: 240px;
              height: 64px;
              line-height: 64px;
              padding: 0 15px;
              box-sizing: border-box;
              border: 1px solid #23c2bd;
            }
            .join-ul{
              position: absolute;
              width: 240px;
              height: 130px;
              right: 0;
              top: 64px;
              border: 1px solid #23c2bd;
              box-sizing: border-box;
              z-index: 1000;
              li{
                height: 64px;
                line-height: 64px;
                box-sizing: border-box;
                padding: 0 15px;
                background: #fff;
              }
              li:first-child{
                border-bottom: 1px solid #23c2bd;
              }
            }

          }
          .line-group{
            width: 100%;
            input{
              margin: 0;
            }
            .sex{
              flex: 1 1 auto;
              width: auto;
            }
            .join-ul{
              flex: 1 1 100%;
              width: 100%;
              li{
                width: 100%;
                background:#fff;
              }
            }
            .province, .city, .county {
              flex: 1 1 auto;
              width: auto;
              height: 64px;
              line-height: 64px;
              box-sizing: border-box;
              border: 1px solid #23c2bd;
              p{
                padding: 0 15px;
                box-sizing: border-box;
              }
              .pro-ul {
                position: absolute;
                left: 0;
                top: 64px;
                width: 100%;
                border: 1px solid #23c2bd;
                box-sizing: border-box;
                max-height: 330px;
                overflow-y: auto;
                background: #fff;
                z-index: 1001;
                li{
                  height: 64px;
                  line-height: 64px;
                  padding: 0 15px;
                  box-sizing: border-box;
                  border-bottom: 1px solid #23c2bd;
                }
                li:last-child{
                  border: none;
                }
              }
            }

          }
          .btn {
            width: 200px;
            height: 56px;
            line-height: 56px;
            text-align: center;
            margin: 0 auto;
            color: #fff;
            background: linear-gradient(45deg, #26c2be, #3486b5, #146897);
            background: -webkit-linear-gradient(45deg, #26c2be, #3486b5, #146897);
            background: -moz-linear-gradient(45deg, #26c2be, #3486b5, #146897);
            background: -ms-linear-gradient(45deg, #26c2be, #3486b5, #146897);
          }
        }
      }

    }
  }

  @media screen and (max-width: 768px) {
    .join{
      width: 100%;
      overflow: hidden;

      input {
        -webkit-appearance:none;
        border-radius: 0;
      }
      .pc-banner{
        display: none;
      }
      .wap-banner {
        position: relative;
        height: 265px;
        overflow: hidden;
        margin-top: 80px;
        img {
          width: auto;
          height: 100%;
        }
      }
      .join-table{
        padding: 0 15px 30px 15px;
        h1 {
          font-size: 18px;
          height: 70px;
          line-height: 70px;
          text-align: center;
          span{
            color: #23c2bd;
          }
        }
        .form-table{
          padding:30px 15px;
          border-radius: 10px;
          -webkit-box-shadow: 5px 5px 20px #ddd;
          -moz-box-shadow: 5px 5px 20px #ddd;
          box-shadow: 5px 5px 20px #ddd;
          position: relative;
          .confirm-time {
            position: absolute;
            left: 50%;
            top: 50%;
            margin-left: -100px;
            margin-top: -25px;
          }
          .line{
            display: flex;
            justify-content: space-around;
            position: relative;
            margin-bottom: 30px;
            input {
              flex: 1 1 70%;
              width: 70%;
              height: 56px;
              padding: 0 15px;
              box-sizing: border-box;
              border: 1px solid #23c2bd;
              margin-right: 15px;

            }
            .sex {
              flex: 0 0 30%;
              width: 30%;
              height: 56px;
              line-height: 56px;
              padding: 0 15px;
              box-sizing: border-box;
              border: 1px solid #23c2bd;
            }
            .join-ul{
              position: absolute;
              width: 30%;
              height: 112px;
              right: 0;
              top: 56px;
              border: 1px solid #23c2bd;
              box-sizing: border-box;
              z-index: 1000;
              li{
                height: 56px;
                line-height: 56px;
                box-sizing: border-box;
                padding: 0 15px;
                background: #fff;
              }
              li:first-child{
                border-bottom: 1px solid #23c2bd;
              }
            }

          }
          .line-group{
            width: 100%;
            input{
              margin: 0;
            }
            .sex{
              flex: 1 1 auto;
              width: auto;
            }
            .join-ul{
              flex: 1 1 100%;
              width: 100%;
              li{
                width: 100%;
                background:#fff;
              }
            }
            .province, .city, .county {
              flex: 1 1 auto;
              width: auto;
              height: 56px;
              line-height: 56px;
              box-sizing: border-box;
              border: 1px solid #23c2bd;
              p{
                padding: 0 15px;
                box-sizing: border-box;
              }
              .pro-ul {
                position: absolute;
                left: 0;
                top: 56px;
                width: 100%;
                border: 1px solid #23c2bd;
                box-sizing: border-box;
                max-height: 224px;
                overflow-y: auto;
                background: #fff;
                z-index: 1001;
                li{
                  height: 56px;
                  line-height: 56px;
                  padding: 0 15px;
                  box-sizing: border-box;
                  border-bottom: 1px solid #23c2bd;
                }
                li:last-child{
                  border: none;
                }
              }
            }

          }
          .btn {
            width: 200px;
            height: 56px;
            line-height: 56px;
            text-align: center;
            margin: 0 auto;
            color: #fff;
            background: linear-gradient(45deg, #26c2be, #3486b5, #146897);
            background: -webkit-linear-gradient(45deg, #26c2be, #3486b5, #146897);
            background: -moz-linear-gradient(45deg, #26c2be, #3486b5, #146897);
            background: -ms-linear-gradient(45deg, #26c2be, #3486b5, #146897);
          }
        }
      }
    }
  }
</style>



