import Vue from 'vue'
import Router from 'vue-router'
import Home from 'components/home/home'
import Download from 'components/download/download'
import ShareTime from 'components/ShareTime/ShareTime'
import ST from 'components/ST/ST'
import Join from 'components/join/join'
import News from 'components/news/news'
import NewsCompany from 'components/newsCompany/newsCompany'
import Profile from 'components/profile/profile'
import Resuit from 'components/resuit/resuit'
import Contact from 'components/contact/contact'
import NewsDetail from 'components/NewsDetail/NewsDetail'
import CrowedFunding from 'components/crowedfunding/crowedfunding'
import Energy from 'components/energy/energy'
// const News = () => import('components/news/news')

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/news',
      redirect:'/news/1'
    },
    {
      path: '/',
      name: 'Home',
      // component: Home
    },
    {
      path: '/download',
      name: 'Download',
      // component: Download
    },
    {
      path: '/sharetime',
      name: 'ShareTime',
      // component: ShareTime
    },
    {
      path: '/st',
      name: 'ST',
      // component: ST
    },
    {
      path: '/crowedfunding',
      name: 'CrowedFunding',
      // component: CrowedFunding
    },
    {
      path: '/energy',
      name: 'Energy',
      // component: Energy
    },
    {
      path: '/join',
      name: 'Join',
      // component: Join
    },
    {
      path: '/news/:id',
      name: 'News',
      // component: News
    },
    {
      path: '/newscompany/:id',
      name: 'NewsCompany',
      // component: NewsCompany
    },
    {
      path: '/newsdetail',
      name: 'NewsDetail',
      // component: NewsDetail
    },
    {
      path: '/profile',
      name: 'Profile',
      // component: Profile
    },
    {
      path: '/resuit',
      name: 'Resuit',
      // component: Resuit
    },
    {
      path: '/contact',
      name: 'Contact',
      // component: Contact
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})



