<template>
  <div class="news-company">
    <news-content :newsList="newsList"></news-content>
  </div>
</template>

<script>
  import NewsContent from 'base/newsContent/newsContent'
  import {axiosGet} from "../../api/config"

  export default {
    data() {
      return{
        newsList: []
      }
    },
    created() {
      let {id} = this.$route.params
      this.loadData(id)
    },

    methods: {
      loadData(id) {
        this.newsList = []
        axiosGet(`/news/${id}`).then(res => {
          this.newsList = res.data.data
        })

      }
    },
    components: {
      NewsContent
    },
    watch: {
      // '$route': (to, from) =>{
      //   let {id} = to.params
      //   this.a.methods.loadData(id)
      // }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">

</style>



