<template>
  <div class="contact-c">
    <div class="banner">
      <img src="./images/banner.jpg" alt="">
    </div>
    <div class="contact-b clearfix">
      <h2 class="wow slideInDown pc-show">联系<span>方式</span></h2>
      <div id="concact-container"></div>
      <div class="contact-content">
        <h1 class="wow slideInLeft" data-wow-delay="0.2s">{{contactList.title}}</h1>
        <p class="wow slideInLeft" data-wow-delay="0.4s">
          地址：{{contactList.address}}
        </p>
        <p class="wow slideInLeft" data-wow-delay="0.6s">
          邮箱：{{contactList.email}}
        </p>
        <p class="wow slideInLeft" data-wow-delay="0.8s">
          电话：{{contactList.tel}}
        </p>
        <p class="wow slideInLeft" data-wow-delay="1s">
          邮编：{{contactList.postcode}}
        </p>
      </div>

    </div>
    <div class="logo">
      <div class="logo-content">
        <h2 class="wow slideInDown">商务<span>合作</span></h2>
        <ul class="wow bounceIn" data-wow-delay="1s">
          <li v-for="(item, index) in name">
            <!--<a><img :src="item" alt=""></a>-->
            <h3>{{name[index]}}</h3>
            <div class="txt">
              <p>联系人：{{contacts[index]}}</p>
              <!--<p>电话：{{tel[index]}}</p>-->
              <p>邮箱：{{email[index]}}</p>
            </div>
          </li>
        </ul>
      </div>

    </div>
    <!--<b-footer class="b-footer"></b-footer>-->
  </div>
</template>

<script>
  import BFooter from 'base/footer/footer'
  import {axiosGet} from "../../api/config"
  import wow from '../../../node_modules/wowjs/dist/wow.js'
  import '../../../node_modules/wowjs/css/libs/animate.css'

  export default {
    data() {
      return {
        contactList: [],
        tel: [],
        name: [],
        contacts: [],
        email: []
      }
    },
    created() {
      axiosGet('/about').then(res => {
        if(res.status === 200) {
          this.contactList = res.data[0]
        }
      })
      axiosGet('/sections').then(res => {
        if(res.status === 200) {
          res.data.map(item => {
            this.name.push(item.title)
            this.tel.push(item.tel)
            this.contacts.push(item.contacts)
            this.email.push(item.email)
          })
        }
      })
    },
    mounted() {
      this.$nextTick(() => {
        this.loadMap()
      })

    },
    methods: {
      loadMap() {
        let _this = this
        this.map = new AMap.Map('concact-container', {
          zoom: 16
        })
        this.map.setZoomAndCenter(20, [117.696678, 24.504995])
        this.map.setFitView()
      },
    },
    components: {
      BFooter
    },
    watch: {
      contactList() {
        this.$nextTick(() => {
          let wowDoc = new wow.WOW(
            {
              boxClass:     'wow',
              animateClass: 'animated',
              offset:       0,
              mobile:       true,
              live:         true
            }
          )
          wowDoc.init()
        })
      }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  @import "~common/css/base.scss";

  @media screen and (min-width: 769px) {
    .contact-c{
      padding-top: 100px;
      .wap-show{
        display: none;
      }
      .banner {
        width: auto;
        max-height: 532px;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .contact-b {
        width: 1200px;
        margin: 0 auto;
        #concact-container {
          width: 584px;
          height: 288px;
          float: right;
          border: 1px solid #ddd;
        }
        .contact-content {
          float: left;
          width: 500px;
          height: auto;
          padding: 45px 0;
          h1{
            font-size: 24px;
            margin-bottom: 30px;
          }
          p{
            font-size: 14px;
            margin: 10px 0;
            line-height: 26px;
            padding-left: 40px;
            background: url('./images/Connect_us_icon5.png') no-repeat left;
            background-size: 26px 26px;
          }
          p:nth-of-type(2) {
            background: url('./images/Connect_us_icon3.png') no-repeat left;
            background-size: 26px 26px;
          }
          p:nth-of-type(3) {
            background: url('./images/Connect_us_icon2.png') no-repeat left;
            background-size: 26px 26px;
          }
          p:nth-of-type(4) {
            background: url('./images/Connect_us_icon4.png') no-repeat left;
            background-size: 26px 26px;
          }
        }
        #concact-container:after{
          content:'';
          position:absolute;
          left:50%;
          top:50%;
          width:24px;
          height:30px;
          margin:-25px 0 0 24px;
          background:url("./images/map-marker.png") no-repeat;
          -webkit-background-size:24px 30px;
          -moz-background-size:24px 30px;
          background-size:24px 30px;
          z-index: 10001
        }
      }
      h2{
        font-size: 40px;
        height: 150px;
        line-height: 150px;
        text-align: center;
        span{
          color: #23c2bd;
        }
      }
      .logo {
        background: #f2f2f2;
        margin-top: 20px;
        padding-bottom: 20px;
        .logo-content{
          @include width-content();
          margin: 0 auto 60px;
          h2 {
            height: 100px;
            line-height: 100px;
            border-bottom: 1px solid #ddd;
          }
          ul{
            font-size: 0;
            position: relative;
            li{
              border-bottom: 1px solid #dfdfdf;
              padding: 38px 0 34px;
              background: url(./images/icon39.png) left 48px no-repeat;
              box-sizing: border-box;
              padding-left: 50px;
              width: 25%;
              display: inline-block;
              height: auto;
              max-height: 194px;
              vertical-align: top;
              h3 {
                font-size: 20px;
                line-height: 38px;
              }
              .txt {
                font-size: 14px;
                color: #666;
                line-height: 24px;
                min-height: 72px;
              }
            }
          }
        }
      }

    }
  }

  @media screen and (max-width: 768px) {
    .contact-c {
      padding-top: 80px;
      .pc-show {
        display: none;
      }
      .banner {
        display: none;
      }
      #concact-container {
        width: 100%;
        height: 200px;
      }
      #concact-container:after{
        content:'';
        position:absolute;
        left:50%;
        top:50%;
        width:24px;
        height:30px;
        margin:-25px 0 0 24px;
        background:url("./images/map-marker.png") no-repeat;
        -webkit-background-size:24px 30px;
        -moz-background-size:24px 30px;
        background-size:24px 30px;
        z-index: 10001
      }
      /*
      .contact-content {
        position: absolute;
        width: 300px;
        height: 206px;
        left: 50%;
        top: 50%;
        margin: -236px 0 0 -150px;
        background: #fff;
        padding: 15px;
        z-index: 10001;
        box-sizing: border-box;
        border-radius: 10px;
        h1{
          font-size: 18px;
          margin-bottom: 30px;
        }
        p{
          font-size: 14px;
          margin: 10px 0;
          line-height: 22px;
        }
      }
      .contact-content::after{
        content: '';
        position: absolute;
        z-index: 1000;
        bottom: -8px;
        left: 50%;
        margin-left: 28px;
        border-left: 8px solid transparent;
        border-right: 8px solid transparent;
        border-top: 8px solid #fff;
      }
      */
      .contact-content {
        width: 100%;
        padding: 30px 15px;
        box-sizing: border-box;
        h1{
          font-size: 20px;
          margin-bottom: 20px;
          text-align: center;
          color: #49c5ff;
        }
        p{
          font-size: 14px;
          margin: 10px 0;
          line-height: 26px;
          padding-left: 40px;
          background: url('./images/Connect_us_icon5.png') no-repeat left;
          background-size: 26px 26px;
        }
        p:nth-of-type(2) {
          background: url('./images/Connect_us_icon3.png') no-repeat left;
          background-size: 26px 26px;
        }
        p:nth-of-type(3) {
          background: url('./images/Connect_us_icon2.png') no-repeat left;
          background-size: 26px 26px;
        }
        p:nth-of-type(4) {
          background: url('./images/Connect_us_icon4.png') no-repeat left;
          background-size: 26px 26px;
        }
      }
      .logo {
        background: #f2f2f2;
        padding-bottom: 20px;
        .logo-content{
          width: 100%;
          h2 {
            height: 80px;
            line-height: 80px;
            border-bottom: 1px solid #ddd;
            font-size:18px;
            text-align: center;
            /*background: #fff;*/
            color: #49c5ff;
          }
          ul{
            font-size: 0;
            position: relative;
            padding: 0 15px;
            li{
              border-bottom: 1px solid #dfdfdf;
              padding: 38px 0 34px;
              background: url(./images/icon39.png) left 48px no-repeat;
              box-sizing: border-box;
              padding-left: 50px;
              width: 100%;
              height: auto;
              vertical-align: top;
              h3 {
                font-size: 20px;
                line-height: 38px;
              }
              .txt {
                font-size: 14px;
                color: #666;
                line-height: 24px;
                min-height: 72px;
              }
            }
          }
        }
      }
      .b-footer {
        display: none;
      }
    }
  }

</style>



