<template>
  <div class="download">
    <div class="pc-banner">
      <img src="./images/banner.png" alt="">
    </div>
    <div class="wap-banner">
      <img src="./images/mini-banner.png" alt="">
    </div>
    <div class="download-app">
      <h3 class="wow slideInDown" ref="slide">下载<span>APP</span>客户端，开启盈利模式</h3>
      <div class="app-info">
        <div class="app-content" v-for="(item,index) in appinfos">
          <i :class="lineData[index]"></i>
          <div class="detail" :class="index%2!==0 ? 'detail-res' : ''">
            <div class="part1">
              <h2 :class="index%2!==0 ? left_right[1] : left_right[0]">{{item.title}}</h2>
              <p :class="index%2!==0 ? left_right[1] : left_right[0]" data-wow-delay=".5s">{{item.content}}</p>
            </div>
            <div class="part2 wow bounceIn" ref="partS">
              <img :src="item.image" alt="">
            </div>
          </div>
        </div>

      </div>
    </div>
    <b-footer></b-footer>
  </div>
</template>

<script>
  import BFooter from 'base/footer/footer'
  import {axiosGet} from "../../api/config"
  import wow from '../../../node_modules/wowjs/dist/wow.js'
  import '../../../node_modules/wowjs/css/libs/animate.css'

  export default {
    data() {
      return {
        appinfos: [],
        lineData: [],
        left_right: []
      }
    },
    created() {
      axiosGet('/appinfos').then(res => {
        if(res.status === 200) {
          this.appinfos = [...res.data]
          this.lineData = ['line', 'line-two', 'line-three', 'line-last']
          this.left_right = ['wow slideInLeft', 'wow slideInRight']
        }
      })

    },
    mounted() {


    },
    components: {
      BFooter
    },
    watch: {
      appinfos() {
        this.$nextTick(() => {
          let wowDoc = new wow.WOW(
            {
              boxClass:     'wow',
              animateClass: 'animated',
              offset:       0,
              mobile:       true,
              live:         true
            }
          )
          this.$refs.slide.style.opacity = 1
          wowDoc.init()
        })
      }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  @import '~common/css/base.scss';

  @media screen and (min-width: 769px) {
    .download{
      @include wrapper();
      .pc-banner{
        position: relative;
        height: 650px;
        overflow: hidden;
        img{
          width: 100%;
          height: auto;
        }
      }
      .wap-banner{
        display: none;
      }
      .download-app{
        width: 100%;

        h3{
          opacity: 0;
          @include width-content();
          margin: 0 auto;
          height: 200px;
          line-height: 200px;
          text-align: center;
          font-size: 28px;
          border-left: 4px solid #5aa2c8;

          span{
            color: #5aa2c8;
          }
        }
        .app-info{
          position: relative;
          height: auto;
          width: 100%;
          .app-content{
            height: 536px;
            i{
              position: absolute;
              width: 240px;
              height: 4px;
              background: linear-gradient(45deg, #44cbc7, #5182a1, #44cbc7);
              background: -webkit-linear-gradient(45deg, #44cbc7, #5182a1, #44cbc7);
              background: -moz-linear-gradient(45deg, #44cbc7, #5182a1, #44cbc7);
              background: -ms-linear-gradient(45deg, #44cbc7, #5182a1, #44cbc7);
            }
            i.line {
              top: 536px;
            }
            i.line-two{
              right: 0;
              top: 1064px;
            }
            i.line-three{
              left: 0;
              top: 1608px;
            }
            i.line-last{
              right: 0;
              top: 2138px;
            }
            .detail {
              @include width-content();
              margin: 0 auto;
              display: flex;
              position: relative;
              .part1{
                flex:1 1 auto;
                width: auto;
                padding: 100px 0 0 130px;
                background: #f7f7f7;
                height: 536px;
                box-sizing: border-box;
                h2{
                  font-size: 32px;
                  line-height: 56px;
                  visibility: hidden;
                }
                p{
                  font-size: 20px;
                  line-height: 24px;
                  margin-top: 60px;
                  visibility: hidden;
                }
              }
              .part2{
                flex: 0 0 536px;
                width: 536px;
                visibility: hidden;
                img{
                  width: 100%;
                  height: 100%;
                }
              }
            }
            .detail-res {
              flex-direction: row-reverse;
              .part1{
                background: #fff;
              }
            }
          }
        }
      }
    }
  }

  @media screen and (max-width: 768px) {
    .download{
      width: 100%;
      overflow: hidden;
      .pc-banner{
        display: none;
      }
      .wap-banner {
        position: relative;
        height: 265px;
        overflow: hidden;
        margin-top: 80px;
        img{
          width: auto;
          height: 100%;
        }
      }
      .download-app{
        width: 100%;

        h3{
          opacity: 0;
          margin: 0 auto;
          height: 70px;
          line-height: 70px;
          text-align: center;
          font-size: 18px;
          span{
            color: #5aa2c8;
          }
        }
        .app-info{
          position: relative;
          height: auto;
          width: 100%;
          .app-content{
            height: 33.333vw;
            padding: 0 15px;
            margin-bottom: 8vw;
            i{
              position: absolute;
              width: 60px;
              height: 2px;
              background: linear-gradient(45deg, #44cbc7, #5182a1, #44cbc7);
              background: -webkit-linear-gradient(45deg, #44cbc7, #5182a1, #44cbc7);
              background: -moz-linear-gradient(45deg, #44cbc7, #5182a1, #44cbc7);
              background: -ms-linear-gradient(45deg, #44cbc7, #5182a1, #44cbc7);
            }
            i.line {
              left: 0;
              top: 33.333vw;
            }
            i.line-two{
              right: 0;
              top: 74.666vw;
            }
            i.line-three{
              left: 0;
              top: 115.999vw;
            }
            i.line-last{
              right: 0;
              top: 160.332vw;
            }
            .detail {
              margin: 0 auto;
              display: flex;
              position: relative;
              .part1{
                flex:1 1 auto;
                width: auto;
                background: #f7f7f7;
                height: 33.333vw;
                padding: 10px;
                box-sizing: border-box;
                h2{
                  font-size: 18px;
                  line-height: 20px;
                  visibility: hidden;
                }
                p{
                  font-size: 12px;
                  line-height: 20px;
                  visibility: hidden;
                  margin-top: 10px;
                }
              }
              .part2{
                flex: 0 0 33.333vw;
                width: 33.333vw;
                visibility: hidden;
                img{
                  width: 100%;
                  height: 100%;
                }
              }
            }
            .detail-res {
              flex-direction: row-reverse;
              .part1{
                background: #fff;
              }
            }
          }
        }
      }
    }
  }

</style>



