<template>
  <div class="resuit" ref="resuit">
    <div class="pc-banner">
      <img src="./images/banner.png" alt="">
    </div>
    <div class="wap-banner">
      <img src="./images/mini-banner.png" alt="">
    </div>
    <div class="resuit-content">
      <h1 class="wow slideInDown">人才<span>招聘</span></h1>
      <div class="resuit-box">
        <ul>
          <li v-for="(item, index) in resuitList" class="wow rotateIn">
            <div class="needman">
              <span>职位名称</span>
              <span>招聘人数：{{item.need}}人</span>
            </div>
            <h3 class="title">{{item.title}}</h3>
            <div class="detail">
              <span>截止时间：{{item.endtime}}</span>
              <span @click="goDetail(index)"><a>查看详情</a></span>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <b-footer></b-footer>
    <confirm :resuitContent="resuitContent" :widthNum="widthNum" ref="confirm"></confirm>
  </div>
</template>

<script>
  import {axiosGet} from "../../api/config"
  import BFooter from 'base/footer/footer'
  import Confirm from 'base/confirm/confirm'
  import wow from '../../../node_modules/wowjs/dist/wow.js'
  import '../../../node_modules/wowjs/css/libs/animate.css'

  export default {
    data() {
      return {
        resuitList: [],
        resuitContent: {},
        hg: 0,
        widthNum: false
      }
    },
    created() {
      axiosGet('/positions').then(res => {
        if(res.status === 200) {
          this.resuitList = this._normalizeData(res.data)
        }

      })
      if(document.body.clientWidth < 768) {
        this.widthNum = true
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.hg = this.$refs.resuit.clientHeight + 244
      })
    },
    methods: {
      goDetail(idx) {
        this.$refs.confirm.show()
        this.resuitContent = this.resuitList[idx]
      },
      _normalizeData(itemArr) {
        let arr = []
        itemArr.map(item => {
          item.endtime = item.endtime.split(' ') [0]
          arr.push(item)
        })
        return arr
      }
    },
    components: {
      BFooter,
      Confirm
    },
    watch: {
      resuitList() {
        this.$nextTick(() => {
          let wowDoc = new wow.WOW(
            {
              boxClass:     'wow',
              animateClass: 'animated',
              offset:       0,
              mobile:       true,
              live:         true
            }
          )
          wowDoc.init()
        })
      }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  @import "~common/css/base.scss";

  @media screen and (min-width: 769px) {
    .resuit {
      @include wrapper();
      .wap-banner {
        display: none;
      }
      .pc-banner {
        position: relative;
        height: 650px;
        overflow: hidden;
        img {
          width: 100%;
          height: auto;
        }
      }
      .resuit-content {
        h1{
          font-size: 40px;
          height: 150px;
          line-height: 150px;
          text-align: center;
          span{
            color: #23c2bd;
          }
        }
        .resuit-box{
          @include width-content();
          margin: 0 auto 60px;
          ul{
            display: flex;
            li{
              flex: 0 0 420px;
              width: 420px;
              height: 240px;
              padding: 36px 30px;
              margin: 0 15px;
              box-sizing: border-box;
              border: 1px solid #eee;
              box-shadow: 2px 2px 1px #eee;

              .needman, .detail{
                display: flex;
                span{
                  flex: 0 0 50%;
                  width: 50%;
                }
                span:last-child {
                  text-align: right;
                }
              }
              .detail {
                margin-top: 78px;
                span:last-child {
                  a{
                    padding:5px 15px;
                    border: 1px solid #23c2bd;
                    color: #23c2bd;
                  }

                }
              }
              .title{
                height: 48px;
                line-height: 48px;
                text-overflow: ellipsis;
                overflow: hidden;
                white-space: nowrap;
              }
            }
          }
        }
      }
    }
  }

  @media screen and (max-width: 768px) {
    .resuit {
      width: 100%;
      overflow: hidden;
      .pc-banner{
        display: none;
      }
      .wap-banner {
        position: relative;
        height: 265px;
        overflow: hidden;
        margin-top: 80px;
        img {
          width: auto;
          height: 100%;
        }
      }
      .resuit-content {
        h1{
          font-size: 18px;
          height: 70px;
          line-height: 70px;
          text-align: center;
          span{
            color: #23c2bd;
          }
        }
        .resuit-box{
          padding: 0 15px;
          ul{
            display: flex;
            flex-wrap: wrap;
            li{
              flex: 0 0 100%;
              width: 100%;
              height: 140px;
              padding: 15px;
              box-sizing: border-box;
              border: 1px solid #eee;
              box-shadow: 2px 2px 1px #eee;
              font-size: 14px;
              margin-bottom: 15px;
              .needman, .detail{
                display: flex;
                span{
                  flex: 0 0 50%;
                  width: 50%;
                }
                span:last-child {
                  text-align: right;
                }
              }
              .detail {
                margin-top: 20px;
                span:last-child {
                  a{
                    padding:5px 15px;
                    border: 1px solid #23c2bd;
                    color: #23c2bd;
                  }

                }
              }
              .title{
                height: 48px;
                line-height: 48px;
                text-overflow: ellipsis;
                overflow: hidden;
                white-space: nowrap;
              }
            }
          }
        }
      }
    }
  }
</style>



