<template>
  <div class="crowed-funding">
    <div class="pc-banner">
      <img src="./images/banner.png" alt="">
    </div>
    <div class="wap-banner">
      <img src="./images/mini-banner.png" alt="">
    </div>
    <div class="crowed-content">
      <h1 class="wow slideInDown">佰昌<span>众筹</span></h1>
      <div class="crowed-container wow slideInDown">
        佰昌传媒已与D-Car达成战略合作协议，双方将携手打造新能源汽车在线交易平台，<br/>
        而ST平台加盟成员可享受使用流量值认筹平台上出售的新能源汽车，在完成交易之后，投入的流量值返还，赚取售出差价收益，相当于0投入。
      </div>
      <div class="crowed-img wow bounceIn">
        <img src="./images/c.png" alt="" class="pc-c">
        <img src="./images/mini-c.jpg" alt="" class="wap-c">
      </div>
      <div class="crowed-word">
        <ul>
          <li class="wow slideInLeft" data-wow-delay="0.1s">流量值认筹新能源汽车，可整车认购、也可占股一定比例</li>
          <li class="wow slideInLeft" data-wow-delay="0.2s">交易平台售出众筹产品</li>
          <li class="wow slideInLeft" data-wow-delay="0.3s">原流量值返还，以及获得售出差价的收益</li>
        </ul>
      </div>
    </div>
    <b-footer></b-footer>
  </div>
</template>

<script>
  import wow from '../../../node_modules/wowjs/dist/wow.js'
  import '../../../node_modules/wowjs/css/libs/animate.css'
  import BFooter from 'base/footer/footer'

  export default {
    mounted() {
      this.$nextTick(() => {
        let wowDoc = new wow.WOW(
          {
            boxClass:     'wow',
            animateClass: 'animated',
            offset:       0,
            mobile:       true,
            live:         true
          }
        )
        wowDoc.init()
      })
    },
  components: {
    BFooter
  }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  @import '~common/css/base.scss';

  @media screen and (min-width: 769px) {
    .crowed-funding {
      @include wrapper();
      .pc-banner {
        position: relative;
        height: 650px;
        overflow: hidden;
        img {
          width: 100%;
          height: auto;
        }
      }
      .wap-banner {
        display: none;
      }
      h1 {
        height: 160px;
        line-height: 160px;
        font-size: 36px;
        text-align: center;
        span {
          color: #7dd7d7;
        }
      }
      .crowed-content {
        .crowed-container{
          text-align: center;
          line-height: 24px;

        }
        .crowed-img {
          width: 1320px;
          margin: 80px auto 0;
          height: auto;
          img{
            width: 100%;
            height: 100%;
          }
          img.wap-c {
            display: none;
          }
          padding-bottom: 30px;
        }
        .crowed-word{
          width: 100%;
          height: auto;
          padding: 20px 0;
          background: #f1f1f1;
          ul{
            display: table;
            margin: 0 auto;
            li {
              padding-left: 15px;
              background: url('./images/dot.png') no-repeat left center;
              background-size: 6px 6px;
              height: 48px;
              line-height: 48px;
            }
          }
        }
      }

    }
  }
  @media screen and (max-width: 768px) {
    .crowed-funding {
      width: 100%;
      overflow: hidden;
      .pc-banner{
        display: none;
      }
      .wap-banner {
        position: relative;
        height: 265px;
        overflow: hidden;
        margin-top: 80px;
        img{
          width: auto;
          height: 100%;
        }
      }
      h1 {
        height: 80px;
        line-height: 80px;
        font-size: 16px;
        text-align: center;
        span{
          color: #7dd7d7;
        }
      }
      .crowed-content {

        .crowed-container{
          padding: 0 15px;
          text-align: center;
          line-height: 24px;
          font-size: 14px;
        }
        .crowed-img {
          width: 100%;
          height: auto;
          margin-top: 30px;
          img{
            width: 100%;
            height: 100%;
            padding: 0 10px;
            box-sizing: border-box;
          }
          img.pc-c {
            display: none;
          }
          padding-bottom: 30px;
        }
        .crowed-word{
          width: 100%;
          height: auto;
          padding: 20px 15px;
          background: #f1f1f1;
          box-sizing: border-box;
          ul{
            display: table;
            margin: 0 auto;
            li {
              padding-left: 15px;
              background: url('./images/dot.png') no-repeat left 10px;
              background-size: 6px 6px;
              line-height: 24px;
              font-size: 14px;
            }
          }
        }
      }
    }
  }
</style>



