              }
              p{
                text-align: center;
                height: 24px;
                line-height: 24px;
                font-weight: bold;
                font-size: 14px;
              }
              p.big-font {
                margin-top: 5px;
              }
            }
          }

        }
      }
    }
  }
</style>



