<template>
  <div class="news-detail">
    <div class="news">
      <div class="pc-banner">
        <img src="./images/banner.png" alt="">
      </div>
      <div class="wap-banner">
        <img src="./images/mini-banner.png" alt="">
      </div>
      <div class="news-detail-container">
        <div class="news-detail-title">
          <h1>{{newsContent.title}}</h1>
          <div class="news-creater">
            <span>时间：{{newsContent.created_at}}</span>
            <span>作者：{{newsContent.host}}</span>
          </div>

        </div>
        <div class="news-detail-content" v-html="newsContent.content"></div>
      </div>
    </div>
    <b-footer></b-footer>
  </div>
</template>

<script>
  import {axiosGet} from "../../api/config"
  import BFooter from 'base/footer/footer'

  export default {
    data() {
      return {
        newsContent: {}
      }
    },
    created() {
      let {id} = this.$route.query
      axiosGet(`/new/${id}`).then(res => {

        this.newsContent = this.normalizeData(res.data)
      })
    },
    methods: {
      normalizeData(res) {
        res.created_at = res.created_at.split(' ')[0]
        return res
      }
    },
    components: {
      BFooter
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  @import '~common/css/base.scss';

  @media screen and (min-width: 769px) {
    .news {
      @include wrapper();
      .pc-banner {
        position: relative;
        height: 650px;
        overflow: hidden;
        img {
          width: 100%;
          height: auto;
        }
      }
      .wap-banner {
        display: none;
      }
      .news-detail-container{
        @include width-content();
        margin: 0 auto;
        .news-detail-title {
          border-bottom: 1px solid #ddd;
          h1 {
            font-size: 40px;
            height: 60px;
            line-height: 60px;
            margin: 20px 0;
            text-align: center;

          }
          .news-creater {
            text-align: center;
            padding-bottom: 20px;
            color: #999;
          }

        }
        .news-detail-content{
          padding: 15px 0;
          line-height: 24px;
          font-size: 14px;
          a{
            font-size: 14px;
          }
        }
      }
    }
  }
  @media screen and (max-width: 768px) {
    .news {
      width: 100%;
      overflow: hidden;
      .pc-banner{
        display: none;
      }
      .wap-banner {
        position: relative;
        height: 265px;
        overflow: hidden;
        margin-top: 80px;
        img {
          width: auto;
          height: 100%;
        }
      }
      .news-detail-container{
        padding: 0 15px;
        .news-detail-title {
          border-bottom: 1px solid #ddd;
          h1 {
            font-size: 18px;
            height: 70px;
            line-height: 70px;
            text-align: center;

          }
          .news-creater {
            text-align: center;
            padding-bottom: 20px;
            color: #999;
            font-size: 12px;
            span:first-child{
              margin-right: 5px;
            }
          }

        }
        .news-detail-content{
          padding: 15px 0;
          line-height: 24px;
          font-size: 14px;
          a{
            font-size: 14px;
          }
          img{
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }
</style>



