<template>
  <header class="b-header">
    <div class="nav-header">
      <aside>
        <img src="./images/logo.png" alt="baichang" class="logo">
        <a class="mini-logo">
          <img src="./images/mini_logo.png" alt="baichang">
        </a>
      </aside>
      <nav class="nav-box">
        <ul class="nav-parent">
          <li v-for="(item, index) in navList" class="nav-li">
            <a class="nav-url" @click.stop="goTo(index)">{{item}}</a>
            <i class="line"></i>
          </li>
        </ul>
        <transition name="fade">
          <ul class="nav-child" v-show="isNavChild" :class="posCls">
            <li class="nav-item" v-for="(item,index) in childNav[childArrNum]" v-show="childArrNum !== 1">
              <a class="item-url" @click.stop="goToChild(index)">{{item}}</a>
            </li>
            <li class="nav-item" v-for="(item,index) in childNav[childArrNum]" v-show="childArrNum === 1">
              <a class="item-url" @click.stop="goToChild(index, newsId[index])">{{item}}</a>
            </li>
          </ul>
        </transition>
      </nav>
      <transition name="slide">
        <nav class="nav-mini-box" v-show="isNavShow">
          <div class="nav-title">
            <div class="dark-logo">
              <img src="./images/dark-logo.png" alt="" />
            </div>
            <div class="close" @click="isNavHideFunc">
              <img src="./images/close.png" alt="">
            </div>
          </div>
          <transition name="slide-center">
            <ul class="nav-parent" v-show="!isNavChild">
              <li v-for="(item, index) in navList" class="nav-li">
                <a class="nav-url" @click.stop="goTo(index,1)">{{item}}</a>
                <i class="line"></i>
              </li>
            </ul>
          </transition>
          <transition name="slide-child-center">
            <ul class="nav-child" v-show="isNavChild" :class="posCls">
              <li class="nav-item" v-for="(item,index) in childNav[childArrNum]" v-show="childArrNum !== 1">
                <a class="item-url" @click.stop="goToChild1(index,1)">{{item}}</a>
              </li>
              <li class="nav-item" v-for="(item,index) in childNav[childArrNum]" v-show="childArrNum === 1">
                <a class="item-url" @click.stop="goToChild(index, newsId[index],1)">{{item}}</a>
              </li>
            </ul>
          </transition>
        </nav>
      </transition>
      <div class="btn-nav-mini" @click.stop="isNavShowFunc">
        <img src="./images/btn_nav_mini.png" alt="">
      </div>
    </div>
  </header>
</template>

<script>
import {axiosGet} from 'api/config'
export default {
  data() {
    return {
      navList:[],
      childNav: [],
      childArrNum: 0,
      aid: 0,
      isNavChild: false,
      posCls: 'nav-child-item1',
      newsId: [],
      isNavShow: false
    }
  },
  created() {
    // this.navList = ['首页', 'SHARE TIME', 'APP下载','在线加盟','佰昌动态', '关于佰昌']
    // this.childNav = [
    //   ['共享平台', '佰昌'],
    //   ['公司简介', '人才招聘','联系我们']
    // ]
    axiosGet('/base').then(res => {
      if(res.status === 200) {

        let tempArr = this._normalizeNav(res.data[0])
        let arr = []
        let arr1 = []
        let strArr = []
        let childArr = []
        let childArr1 = []
        let childArr2 = []
        let arrId = []
        tempArr.map(item => {
          if(item.startsWith('c')) {

            arr.push(res.data[0][item])
          } else {
            strArr.push(item)
            arr1.push(res.data[0][item])
          }
        })
        strArr.map(item => {
          if(item.startsWith('stwo')) {
            childArr.push(res.data[0][item])
          } else {
            childArr2.push(res.data[0][item])
          }
        })
        this.navList = arr

        axiosGet('/categorys').then(res => {
          if(res.status === 200) {
            res.data.map(item => {
              childArr1.push(item.title)
              arrId.push(item.id)
            })
            this.newsId = [...arrId]
            this.childNav = [childArr, childArr1, childArr2]
          }
        })

      }
    })

  },
  methods: {
    goTo(idx, bool) {
      this.aid = idx
      this.isNavChild = false
      if(idx == 1) {
        this.isNavChild = !this.isNavChild
        if(this.childArrNum != 0) {
          this.isNavChild = true
        }
        this.childArrNum = 0
        this.posCls = `nav-child-item${idx}`

        return
      }
      if(idx == 4) {
        this.isNavChild = !this.isNavChild
        if(this.childArrNum != 1) {
          this.isNavChild = true
        }
        this.childArrNum = 1
        this.posCls = `nav-child-item${idx}`
        return
      }

      if(idx == 5) {
        this.isNavChild = !this.isNavChild
        if(this.childArrNum != 2) {
          this.isNavChild = true
        }
        this.childArrNum = 2
        this.posCls = `nav-child-item${idx}`
        return
      }
      if(bool == 1) {
        this.isNavShow = false
      }
      this.$emit('goTo',`${idx}`)
    },
    goToChild(idx, newsId, bool) {
      this.isNavChild = false
      let tempId = `${this.aid}.${idx}`
      if(bool == 1) {
        this.isNavShow = false
      }
      this.$emit('goTo', tempId, newsId)
    },
    goToChild1(idx, bool) {
      this.isNavChild = false
      let tempId = `${this.aid}.${idx}`
      if(bool == 1) {
        this.isNavShow = false
      }
      this.$emit('goTo', tempId)
    },
    hide() {
      this.isNavChild = false
    },
    isNavShowFunc() {
      this.isNavShow = true
    },
    isNavHideFunc() {
      this.isNavShow = false
    },
    _normalizeNav(data) {
      let {keys} = Object
      let arr = []
      let arr1 = []
      for(let item of keys(data)) {
        arr.push(item)
      }
      return arr
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss">
  @import '~common/css/base.scss';

  @media screen and (min-width:769px){
    .b-header{
      position: fixed;
      top: 0;
      left: 0;
      z-index: 10010;
      min-width: 769px;
      width: 100%;
      background: #fff;
      border-bottom: 1px solid #eee;
      padding: 0 30px;
      box-sizing: border-box;
      .nav-header{
        position: relative;
        height: 86px;
        margin: 0 auto;
        padding: 7px 0;
        text-align: right;
        //@include width-content();
        width: 100%;
        .btn-nav-mini{
          display:none;
        }
        aside{
          position: absolute;
          display: inline-block;
          left: 0;
          top: 7px;
          width: 70px;
          height: 86px;
          img{
            width: 100%;
            height: 100%
          }
          .mini-logo{
            display: none;
          }
        }
        .nav-mini-box {
          display: none;
        }
        .nav-box{
          display: inline-block;

          .nav-parent{
            .nav-li{
              display: inline-block;
              height:86px;
              line-height: 86px;
              .nav-url{
                display: block;
                position: relative;
                padding: 0 20px;
                width: 100%;
                font-size: 16px;
                color: #949494;
                text-align: center;
                border-radius: 5px;
                transition: all 0.2s;
              }
              .line{
                display: block;
                position: relative;
                margin-left: 4px;
                left: 50%;
                z-index: 1000;
                width: 34px;
                height: 4px;
                background-color: #000;
                transition: all 0.4s;
                transform: scaleX(0);
              }
            }
            li:hover{
              .nav-url{
                color: #000;
              }
              .line{
                transform: scaleX(1)
              }
            }
          }
          .nav-child{
            position: absolute;

            top: 110px;
            right: 150px;
            width: 164px;
            border-radius: 5px;
            background-color: #fff;
            border: 1px solid #f4f4f4;
            text-align: left;
            .nav-item{
              width: 100%;
              .item-url{
                display: block;
                padding: 14px 0 14px 30px;
                font-size: 16px;
                color: #333;
              }
              .item-url:hover{
                color: #23aaff;
              }
            }

            .nav-item + .nav-item {
              border-top: 1px solid #f4f4f4;
            }
            &.fade-enter-active, &.fade-leave-active {
              transition: all 0.3s
            }

            &.fade-enter, &.fade-leave-active{
              transform: translateY(30%);
              opacity: 0.2;
            }
          }
          .nav-child-item1{
            right: 320px;
          }
          .nav-child-item4{
            right: 10px;
          }
          .nav-child-item5{
            right: 0px;
          }
          .nav-child:after{
            content: '';
            position: absolute;
            z-index: 1000;
            top: -8px;
            left: 30px;
            border-left: 8px solid transparent;
            border-right: 8px solid transparent;
            border-bottom: 8px solid #fff;
          }
          .nav-child-item5:after{
            left: 130px;
          }
        }

      }
    }
  }

  @media screen and (max-width: 768px){
    header{
      width: 100%;
      background: #fff;
      border-bottom: 1px solid #eee;
      height: 100px;
      box-sizing: border-box;
      height: 80px;
      .nav-header{
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 80px;
        position: relative;
        .btn-nav-mini {
          display: block;
          width: 30px;
          height: 20px;
          position: absolute;
          top: 50%;
          margin-top: -10px;
          right: 10px;
          img{
            width: 100%;
            height: 100%;
          }
        }
        aside{
          flex: 0 0 50%;
          width: 50%;

          .logo{
            display:none;
          }
          .mini-logo{
            display: block;
            width: 200px;
            height: 50px;
            margin-left: 10px;
            img{
              width: 100%;
              height: 100%;
            }
          }
        }

        .nav-box {
          display: none;
        }
        .nav-mini-box{
          width: 70%;
          display: block;
          position: absolute;
          top: 0;
          right: 0;
          min-height: 100vh;
          background: #fff;
          border: 1px solid #eee;
          z-index: 1000;
          &.slide-enter-active, &.slide-leave-active{
            transition: all 0.4s;
          }
          &.slide-enter, &.slide-leave-active{
            transform: translate(100%,0%);
            opacity: 0;
          }
          .nav-title{
            display: flex;
            justify-content: space-between;
            align-items: center;
            height: 80px;
            padding: 0 5px;
            background: #2c2a2b;
            .dark-logo{
              width: 200px;
              height: 50px;

            }
            .close{
              width: 20px;
              height: 20px;
            }
            img{
              width: 100%;
              height: 100%;
            }
          }
          ul{
            li{
              width: 100%;
              height:48px;
              line-height: 48px;
              a{
                display: block;
                text-align: center;
                border-bottom: 1px solid #eee;
              }
            }
          }
          .nav-parent {
            &.slide-center-enter-active, &.slide-center-leave-active {
              transition: all 0.4s;
            }

            &.slide-center-enter, &.slide-center-leave-active {
              transform: scaleY(2);
              opacity:0;
            }
          }
          .nav-child{
            &.slide-child-center-enter-active, &.slide-child-center-leave-active {
              transition: all 0.4s;
            }

            &.slide-child-center-enter, &.slide-child-center-leave-active {
              transform: scaleY(0);
              opacity:0;
            }
          }
        }
      }
      nav.active{

      }
    }
  }

</style>




