<template>
  <div id="app" @click="comHide">
    <b-header @goTo="goTo" ref="bheader" class="b-header"></b-header>
    <component :is="homeNull"></component>
    <router-view></router-view>
  </div>
</template>

<script>
import BHeader from 'base/header/header'
import Home from 'components/home/home'
import Download from 'components/download/download'
import ShareTime from 'components/ShareTime/ShareTime'
import ST from 'components/ST/ST'
import Join from 'components/join/join'
import News from 'components/news/news'
import NewsCompany from 'components/newsCompany/newsCompany'
import Profile from 'components/profile/profile'
import Resuit from 'components/resuit/resuit'
import Contact from 'components/contact/contact'
import NewsDetail from 'components/NewsDetail/NewsDetail'
import NullCom from 'base/nullCom/nullCom'
import CrowedFunding from 'components/crowedfunding/crowedfunding'
import Energy from 'components/energy/energy'
import {getData, savaData} from 'common/js/common'

const __KEY__ = '__KEY__'
export default {
  data() {
    return {
      homeNull: ''
    }
  },
  created() {
    this.routeChange()
    if(navigator.appName == "Microsoft Internet Explorer") {
      if(parseInt(navigator.appVersion.split(";")[1].replace(/[ ]/g, "").replace("MSIE",""))<=9) {
        this.homeNull = 'NullCom'
      } else {
        getData(__KEY__).then(res => {
          this.homeNull = res
        }).catch(err => {
          this.homeNull = tempRoute
        })

      }
    } else {
      getData(__KEY__).then(res => {
        this.homeNull = res
      }).catch(err => {
        this.homeNull = tempRoute
      })
    }
  },
  mountd() {

  },

  methods: {
    comHide() {
      this.$refs.bheader.hide()
    },
    routeChange() {
      let tempRoute = ''
      switch (this.$route.name) {
        case 'Home':
          tempRoute = 'Home'
          break
        case 'ShareTime':
          tempRoute = 'ShareTime'
          break
        case 'ST':
          tempRoute = 'ST'
          break
        case 'CrowedFunding':
          tempRoute = 'CrowedFunding'
          break
        case 'Energy':
          tempRoute = 'Energy'
          break
        case 'Download':
          tempRoute = 'Download'
          break
        case 'Join':
          tempRoute = 'Join'
          break
        case 'News':
          tempRoute = 'News'
          break
        case 'NewsCompany':
          tempRoute = 'NewsCompany'
          break
        case 'NewsDetail':
          tempRoute = 'NewsDetail'
          break
        case 'Profile':
          tempRoute = 'Profile'
          break
        case 'Resuit':
          tempRoute = 'Resuit'
          break
        case 'Contact':
          tempRoute = 'Contact'
          break
        default:
          tempRoute = 'NullCom'
      }
      this.homeNull = tempRoute
      savaData(__KEY__, tempRoute)
    },
    goTo(idx, newsId) {
      switch (idx) {
        case '0':
          this.homeNull = 'Home'
          this.$router.push({
            path: '/'
          })
          break
        case '1.0':
          this.homeNull = 'ShareTime'
          this.$router.push({
            path: '/sharetime'
          })
          break
        case '1.1':
          this.homeNull = 'ST'
          this.$router.push({
            path: '/st'
          })
          break
        case '1.2':
          this.homeNull = 'CrowedFunding'
          this.$router.push({
            path: '/crowedfunding'
          })
          break
        case '1.3':
          this.homeNull = 'Energy'
          this.$router.push({
            path: '/energy'
          })
          break
        case '2':
          this.homeNull = 'Download'
          this.$router.push({
            path: '/download'
          })
          break
        case '3':
          this.homeNull = 'Join'
          this.$router.push({
            path: '/join'
          })
          break
        case '4.0':
          this.homeNull = 'News'
          this.$router.push({
            path: `/news/${newsId}`
          })
          break
        case '4.1':
          this.homeNull = 'NewsCompany'
          this.$router.push({
            path: `/newscompany/${newsId}`
          })
          break
        case '5.0':
          this.homeNull = 'Profile'
          this.$router.push({
            path: '/profile'
          })
          break
        case '5.1':
          this.homeNull = 'Resuit'
          this.$router.push({
            path: '/resuit'
          })
          break
        case '5.2':
          this.homeNull = 'Contact'
          this.$router.push({
            path: '/contact'
          })
          break
        default:
          this.homeNull = 'NullCom'
      }

    }
  },
  watch: {
    $route: 'routeChange'
  },
  components: {
    Home,
    BHeader,
    Download,
    ShareTime,
    ST,
    Join,
    News,
    NewsCompany,
    Profile,
    Resuit,
    Contact,
    NewsDetail,
    CrowedFunding,
    Energy,
    NullCom
  }
}
</script>

<style lang="scss" rel="stylesheet/scss">
  @media screen and (max-width: 768px) {
    #app{
      min-width: 100%;
      max-width: 100%;
      .b-header {
        position: fixed;
        width: 100%;
        top: 0;
        z-index: 10008
      }
    }
  }
</style>



