<template>
  <div class="profile">
    <div class="pc-banner">
      <img src="./images/banner.png" alt="">
    </div>
    <div class="wap-banner">
      <img src="./images/mini-banner.png" alt="">
    </div>
    <div class="profile-content">
      <h1 class="wow slideInDown">公司<span>简介</span></h1>
      <div class="wow bounceIn profile-box" data-wow-delay="1s" v-html="profile"></div>
    </div>
    <div class="logo">
      <div class="logo-content">
        <h1 class="wow slideInDown">运营<span>中心</span></h1>
        <ul class="wow bounceIn" data-wow-delay="1s">
          <li v-for="(item,index) in logo">
            <a><img :src="item" alt=""></a>
            <p>
              {{name[index]}}
            </p>
          </li>
        </ul>
      </div>
    </div>
    <b-footer></b-footer>
  </div>
</template>

<script>
  import BFooter from 'base/footer/footer'
  import wow from '../../../node_modules/wowjs/dist/wow.js'
  import '../../../node_modules/wowjs/css/libs/animate.css'
  import {axiosGet} from "../../api/config"

  export default {

    data() {
      return {
        profile: '',
        logo: [],
        name: [],
        tel: [],
        contacts: [],
        address: [],
        page: 1
      }
    },
    created() {
      axiosGet('/company').then(res => {
        if(res.status === 200) {
          this.profile = res.data[0].content
        }
      })
      this.getLogo(this.page)
    },
    mounted() {
      this.$nextTick(() => {
        let wowDoc = new wow.WOW(
          {
            boxClass:     'wow',
            animateClass: 'animated',
            offset:       0,
            mobile:       true,
            live:         true
          }
        )

        wowDoc.init()
      })
    },
    methods: {
      getLogo(page) {
        let tempArr = []
        let nameArr = []
        axiosGet(`/logos?page=${page}`).then(res => {
          res.data.data.map(item => {
            tempArr.push(item.logo)
            nameArr.push(item.name)
            // this.tel.push(item.tel)
            // this.contacts.push(item.contacts)
            // this.address.push(item.adrdetail)
          })
          this.logo.push(...tempArr)
          this.name.push(...nameArr)
          if(page == res.data.last_page) {
            return
          }
          this.page++
          this.getLogo(this.page)
        })
      }
    },
    components: {
      BFooter
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  @import '~common/css/base.scss';

  @media screen and (min-width: 769px) {
    .profile{
      @include wrapper();
      .wap-banner {
        display: none;
      }
      .pc-banner {
        position: relative;
        height: 650px;
        overflow: hidden;
        img {
          width: 100%;
          height: auto;
        }
      }
      h1{
        font-size: 40px;
        height: 150px;
        line-height: 150px;
        text-align: center;
        span{
          color: #23c2bd;
        }
      }
      .profile-content {
        .profile-box {
          @include width-content();
          margin: 0 auto 40px;
          padding: 30px;
          box-shadow: 5px 5px 20px #ddd;
          border-radius: 8px;
          line-height: 24px;
          img{
            max-width: 100%;
            max-height: 100%;
          }
        }
      }
      .logo{
        @include width-content();
        margin: 0 auto 60px;
        ul{
          display: flex;
          flex-wrap: wrap;
          justify-content: left;
          li{
            width: 160px;
            height: auto;
            margin: 0 30px 15px 30px;
            a{
              width: 160px;
              height: 160px;
              display: block;
              img {
                width: 100%;
                height: 100%;
                border-radius: 160px;
              }
            }
            p {
              height: 36px;
              line-height: 36px;
              text-align: center;
            }
          }
        }
      }
    }
  }

  @media screen and (max-width: 768px) {
    .profile{
      width: 100%;
      overflow: hidden;
      .pc-banner{
        display: none;
      }
      .wap-banner {
        position: relative;
        height: 265px;
        overflow: hidden;
        margin-top: 80px;
        img {
          width: auto;
          height: 100%;
        }
      }
      h1{
        font-size: 20px;
        height: 70px;
        line-height: 70px;
        text-align: center;
        span{
          color: #23c2bd;
        }
      }
      .profile-content {
        padding: 0 15px;
        .profile-box {
          padding: 15px;
          box-shadow: 5px 5px 20px #ddd;
          border-radius: 8px;
          line-height: 24px;
          img{
            max-width: 100%;
            max-height: 100%;
          }
        }
      }
      .logo{
        padding: 0 15px;
        ul{
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          li{
            width: 80px;
            height: auto;
            margin-bottom: 15px;
            a{
              width: 80px;
              height: 80px;
              display: block;
              img {
                width: 100%;
                height: 100%;
                border-radius: 80px;
              }
            }
            p {
              height: 36px;
              line-height: 36px;
              text-align: center;
            }
          }
        }
      }
    }
  }
</style>



