<template>
  <div class="news">
    <news-content :newsList="newsList"></news-content>
  </div>
</template>

<script>
  import NewsContent from 'base/newsContent/newsContent'
  import {axiosGet} from "../../api/config"

  export default {
    data() {
      return{
        newsList: [],
        page: 1,
        aid: -1,
      }
    },
    created() {
      let {id} = this.$route.params
      this.aid = id
      this.loadData(this.aid, this.page)
    },

    methods: {
      loadData(id, page) {
        let _this = this
        let tempArr = []
        axiosGet(`/news/${id}?${page}`).then(res => {
          if(res.status === 200) {
            tempArr = res.data.data
            this.newsList.push(...tempArr)
            if(res.data.last_page == this.page) {
              return
            }
            this.page++
            _this.loadData(this.aid, this.page)
          }
        })

      }
    },
    components: {
      NewsContent
    },
    watch: {
      // '$route': (to, from) =>{
      //   let {id} = to.params
      //   this.a.methods.loadData(id)
      // }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">

</style>



