<template>
  <div class="confirm-time" v-show="showFlag">
    {{msg}}
  </div>
</template>

<script>
  export default {
    props: {
      msg:{
        type: String,
        value: ''
      }
    },
    data() {
      return {
        showFlag: false
      }
    },
    beforeDestroy() {
      clearTimeout(this.timer)
    },
    methods: {
      show() {
        clearTimeout(this.timer)
        this.showFlag = true
        this.timer = setTimeout(() => {
          this.showFlag = false
        }, 2000)
      },
      hide() {
        this.showFlag = false
      }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  .confirm-time {
    width: 200px;
    height: 50px;
    line-height: 50px;
    border-radius: 8px;
    padding: 0 10px;
    text-align: center;
    background: rgba(0, 0, 0, .5);
    color: #fff;
    box-sizing: border-box;
    z-index: 9999;
  }
</style>



