<script>

import Banner from 'base/banner/banner'
import {swiper, swiperSlide} from 'vue-awesome-swiper'
import ICountUp from 'vue-countup-v2';
import {axiosGet} from "../../api/config"
import BFooter from 'base/footer/footer'
import wow from '../../../node_modules/wowjs/dist/wow.js'
import '../../../node_modules/wowjs/css/libs/animate.css'


export default {
  data() {
    return {
      bannerList: [],
      showFlag: false,
      endValStr: ['覆盖城市','真实流量', '手机终端'],
      bannerOption: {
        slidesPerView: 3,
        loop: true,
        observer:true,
        autoplay: false,
        watchSlidesProgress: true,
        normalizeSlideIndex: false,
        navigation:{
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      },
      mobileBannerOption: {
        loop: true,
        autoplay: true,
        navigation:{
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      },
      startVal: [0, 0, 0],
      endVal: [30, 1000000, 6000],
      decimals: 0,
      duration: 5,
      options: {
        useEasing: true,
        useGrouping: true,
        separator: ',',
        decimal: '.',
        prefix: '',
        suffix: ''
      }

    }
  },
  computed: {
    swiper() {
      return this.$refs.banner.swiper
    }
  },
  created() {
    axiosGet('/banners').then(res => {
      if(res.status === 200) {
        this.bannerList = res.data
      }
    })
  },
  mounted() {
    let _this = this
    this.$nextTick(() => {
      let wowDoc = new wow.WOW(
        {
          boxClass:     'wow',
          animateClass: 'animated',
          offset:       0,
          mobile:       true,
          live:         true
        }
      )

      wowDoc.init()
      console.log()
      window.addEventListener('scroll',function(){
        if(window.pageYOffset + window.innerHeight >= 2600 && document.body.clientWidth <= 768){
          _this.start()
        } else if(window.pageYOffset + window.innerHeight >= 2700 && document.body.clientWidth >= 769) {
          _this.start()
        }
      },false)
    })

  },
  methods: {
    start() {
      this.$refs.iCount.map(item => {
        item.init()
      })
    }
  },
  components: {
    Banner,
    swiper,
    swiperSlide,
    ICountUp,
    BFooter
  },

}
</script>
