          background: -webkit-linear-gradient(45deg, #74cccc, #5182a1, #74cccc);
          ul{
            display: flex;
            align-items: center;
            height: 100px;
            li{
              color: #fff;
              flex: 1 1 33.333%;
              width: 33.333%;
              text-align: center;
              position: relative;
              font-size: 12px;
              border-right: 1px solid #fff;
              .i-count{
                font-size: 14px;
              }
              i{
                display: none;
              }
            }
            li:last-child {
              border: none;
            }
          }
        }
      }
    }
  }
}
</style>





