<template>
  <div class="news">
    <div class="pc-banner">
      <img src="./images/banner.png" alt="">
    </div>
    <div class="wap-banner">
      <img src="./images/mini-banner.png" alt="">
    </div>
    <div class="news-container" v-if="newsList.length">
      <h1>佰昌<span>新闻</span></h1>
      <div class="news-content">
        <div class="new-news">
          <div class="news-detail">
            <h3>{{newsList[0].title}}</h3>
            <small>{{newsList[0].created_at}}</small>
            <div class="t_p" v-html="newsList[0].content"></div>
            <a class="more" @click="goDetail(newsList[0].id)">查看更多</a>
          </div>
          <aside>
            <img :src="newsList[0].image" alt="">
          </aside>
        </div>

      </div>
      <div class="news-box">
        <ul class="clearfix pc-ul">
          <li v-for="item in newsData">
            <a>
              <span>{{item.created_at}}</span>
              <h2>{{item.title}}</h2>
              <div class="pic zoom"><span><img :src="item.image" alt=""></span></div>
              <div class="txt_p" v-html="item.content"></div>
              <div class="more" @click="goDetail(item.id)">查看更多</div>
            </a>
          </li>
        </ul>
        <ul class="wap-ul">
          <li v-for="item in newsData">
            <a>
              <span>{{item.created_at}}</span>
              <h2>{{item.title}}</h2>
              <div class="pic zoom"><span><img :src="item.image" alt=""></span></div>
              <div class="txt_p" v-html="item.content"></div>
              <div class="more" @click="goDetail(item.id)">查看更多</div>
            </a>
          </li>
        </ul>
      </div>
    </div>
    <b-footer></b-footer>
  </div>
</template>

<script>
  import BFooter from 'base/footer/footer'
  export default {
    props: {
      newsList: {
        type: Array,
        value: []
      }
    },
    data() {
      return {
        newsData: [],
        firstContent: []
      }
    },
    created() {
      this.newsData = this.newsList.slice(1)
    },
    components: {
      BFooter
    },
    methods: {
      goDetail(id) {
        this.$router.push({
          path: '/newsdetail',
          query: {
            id
          }
        })
      }
    },
    watch: {
      newsList(newVal, oldVal) {
        if(newVal.length){
          this.newsList = newVal
          this.newsData = newVal.slice(1)
        }

      }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  @import '~common/css/base.scss';

  @media screen and (min-width: 769px) {
    .news {
      @include wrapper();
      .pc-banner {
        position: relative;
        height: 650px;
        overflow: hidden;
        img {
          width: 100%;
          height: auto;
        }
      }
      .wap-banner {
        display: none;
      }
      .news-container{

        h1 {
          font-size: 40px;
          height: 150px;
          line-height: 150px;
          text-align: center;
          span{
            color: #23c2bd;
          }
        }
        .news-content{
          background: #f2f2f2;
          .new-news {
            @include width-content();
            margin: 0 auto;
            display: flex;
            .news-detail {
              flex: 0 0 630px;
              width: 630px;
              height: 470px;
              padding: 100px 50px 50px 0;
              box-sizing: border-box;
              h3 {
                font-size: 28px;
                text-overflow: ellipsis;
                overflow: hidden;
                white-space: nowrap;
              }
              small{
                font-size: 12px;
                color: #999;
                margin: 20px 0 30px 0;
                display: block;
              }
              .t_p {
                position:relative;
                line-height:24px;
                /* 3 times the line-height to show 3 lines */
                height: 72px;
                overflow:hidden;
              }
              .more {
                background: linear-gradient(45deg, #26c2be, #3486b5, #146897);
                background: -moz-linear-gradient(45deg, #26c2be, #3486b5, #146897);
                background: -ms-linear-gradient(45deg, #26c2be, #3486b5, #146897);
                background: -webkit-linear-gradient(45deg, #26c2be, #3486b5, #146897);
                display: block;
                width: 150px;
                height: 48px;
                line-height: 48px;
                text-align: center;
                color: #fff;
                margin-top: 50px;
              }
            }
            aside{
              flex: 0 0 690px;
              width: 690px;
              display: block;
              height: 470px;
              overflow: hidden;
              img{
                width: 100%;
                height: 100%;
                transition: all 1s;
                transform: scale(1);
              }
            }
            aside:hover {
              img{
                transition: all 1s;
                transform: scale(1.1);
              }

            }
          }

        }
        .news-box {
          width: 1400px;
          margin: 60px auto 0;
          .wap-ul{
            display: none;
          }
          ul{
            margin-bottom: 30px;
            li {
              float: left;
              margin-bottom: 30px;
              padding: 24px 30px;
              width: calc((100% - 33px * 3) / 4);
              width: -webkit-calc((100% - 33px * 3) / 4);
              width: -moz-calc((100% - 33px * 3) / 4);
              margin-right: 33px;
              background: #fff;
              border: 1px solid #dbdbdb;
              box-sizing: border-box;

              span{
                display: block;
                line-height: 24px;
                color: #888;
                margin-bottom: 10px;
              }
              h2{
                position:relative;
                margin-bottom: 12px;
                font-size: 18px;
                color: #444;
                height: 60px;
                line-height: 30px;
                transition: all 0.3s;
                overflow: hidden;
              }
              .pic{
                position: relative;
                width: 100%;
                height: auto;
                span{
                  display: block;
                  width: 100%;
                  height: auto;
                  overflow: hidden;
                  img{
                    width: 100%;
                    height: auto;
                    max-height: 180px;

                    transform: scale(1);
                    transition: all 1s ease;
                  }
                }
              }
              .txt_p{
                margin: 20px 0;
                color: #999;
                line-height: 22px;
                height: 44px;
                overflow: hidden;
                position: relative;
                transition: all 0.3s;
              }
              .more{
                display: block;
                width: 120px;
                height: 36px;
                line-height: 36px;
                background: #c3c3c3;
                text-align: center;
                transition: all 0.3s;
                color: #fff;
              }
            }
            li:hover{
              h2, .txt_p{
                color: #45ccc8;
              }
              .pic{
                span{
                  img{
                    transform: scale(1.2);
                  }

                }
              }
              .more {
                background: #45ccc8;
              }
            }
            li:last-child{
              margin-right: 0;
            }
          }
        }
      }
    }
  }
  @media screen and (max-width: 768px) {
    .news{
      width: 100%;
      overflow: hidden;
      .pc-banner{
        display: none;
      }
      .wap-banner {
        position: relative;
        height: 265px;
        overflow: hidden;
        margin-top: 80px;
        img {
          width: auto;
          height: 100%;
        }
      }
      .news-container{

        h1 {
          font-size: 18px;
          height: 70px;
          line-height: 70px;
          text-align: center;
          span{
            color: #23c2bd;
          }
        }
        .news-content{
          background: #f2f2f2;
          .new-news {
            display: flex;
            width: 100%;
            .news-detail {
              flex: 0 0 60vw;
              width: 60vw;
              height: 40vw;
              box-sizing: border-box;
              padding: 15px;
              h3 {
                font-size: 18px;
                text-overflow: ellipsis;
                overflow: hidden;
                white-space: nowrap;
              }
              small{
                font-size: 12px;
                color: #999;
                margin: 10px 0;
                display: block;
              }
              .t_p {
                position:relative;
                line-height:24px;
                height: 24px;
                text-overflow: ellipsis;
                white-space: nowrap;
                overflow:hidden;
                font-size: 12px;
                p{
                  margin: 0 !important;
                  text-overflow: ellipsis;
                  white-space: nowrap;
                  overflow:hidden;
                }
              }
              .more {
                background: linear-gradient(45deg, #26c2be, #3486b5, #146897);
                background: -moz-linear-gradient(45deg, #26c2be, #3486b5, #146897);
                background: -ms-linear-gradient(45deg, #26c2be, #3486b5, #146897);
                background: -webkit-linear-gradient(45deg, #26c2be, #3486b5, #146897);
                display: block;
                width: 75px;
                height: 36px;
                line-height: 36px;
                font-size: 14px;
                text-align: center;
                color: #fff;
                margin-top: 10px;
              }
            }
            aside{
              flex: 0 0 40vw;
              width: 40vw;
              display: block;
              height: 40vw;
              overflow: hidden;
              img{
                width: 100%;
                height: 100%;
                transition: all 1s;
                transform: scale(1);
              }
            }
            aside:hover {
              img{
                transition: all 1s;
                transform: scale(1.1);
              }

            }
          }

        }
        .news-box {
          padding: 0 15px;
          margin-top: 15px;
          .pc-ul{
            display: none;
          }
          ul {
            margin-bottom: 30px;
            display: flex;
            justify-content: space-between;
            width: 100%;
            flex-wrap: wrap;
            li {
              flex: 0 0 48%;
              width: 48%;
              background: #fff;
              border: 1px solid #dbdbdb;
              box-sizing: border-box;
              padding: 10px;
              box-sizing: border-box;
              margin-bottom: 15px;
              span {
                display: block;
                line-height: 10px;
                color: #888;
                font-size: 12px;
                margin-bottom: 10px;
              }
              h2 {
                position: relative;
                margin-bottom: 12px;
                font-size: 16px;
                color: #444;
                height: 30px;
                line-height: 30px;
                transition: all 0.3s;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
              }
              .pic {
                position: relative;
                width: 100%;
                max-height: 21.333vw;
                overflow: hidden;
                span {
                  display: block;
                  width: 100%;
                  height: auto;
                  overflow: hidden;
                  img {
                    width: 100%;
                    height: auto;
                    max-height: 180px;
                  }
                }
              }
              .txt_p {
                margin: 20px 0;
                color: #999;
                line-height: 22px;
                height: 44px;
                font-size: 14px;
                overflow : hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
              }
              .more {
                display: block;
                width: 100%;
                height: 36px;
                line-height: 36px;
                text-align: center;
                color: #fff;
                background: linear-gradient(45deg, #26c2be, #3486b5, #146897);
                background: -moz-linear-gradient(45deg, #26c2be, #3486b5, #146897);
                background: -ms-linear-gradient(45deg, #26c2be, #3486b5, #146897);
                background: -webkit-linear-gradient(45deg, #26c2be, #3486b5, #146897);
              }
            }
          }
        }
      }
    }
  }

</style>



