<template>
  <div class="confirm" ref="confirm" v-show="showFlag" @click="hide">
    <div class="confirm-box" v-if="!widthNum">
      <h1>{{resuitContent.title}}</h1>
      <div class="tip">
        <small>截止时间：{{resuitContent.endtime}}</small>
        <span>招聘人数：{{resuitContent.need}}</span>
      </div>
      <div class="resuit-content" v-html="resuitContent.content"></div>
      <a class="btn">
        简历请投递至hr@bccmst.com
      </a>
    </div>
    <div class="confirm-box confirm-box-width" v-else>
      <h1>{{resuitContent.title}}</h1>
      <div class="tip">
        <small>截止时间：{{resuitContent.endtime}}</small>
        <span>招聘人数：{{resuitContent.need}}</span>
      </div>
      <div class="resuit-content" v-html="resuitContent.content"></div>
      <a class="btn">
        简历请投递至hr@bccmst.com
      </a>
    </div>
  </div>
</template>

<script>
  export default {
    props: {
      resuitContent: {
        type: Object,
        value: ''
      },
      widthNum: {
        type: Boolean,
        value: false
      }
    },
    data() {
      return {
        showFlag: false
      }
    },
    methods: {
      show() {
        this.showFlag = true
      },
      hide() {
        this.showFlag = false
      }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss">
  .confirm {
    width: 100%;
    position: fixed;
    z-index: 10000;
    background: rgba(0, 0, 0, .5);
    bottom: 0;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    .confirm-box {
      width: 500px;
      height: auto;
      padding:40px;
      background: #fff;
      border-radius: 10px;
      h1{
        font-size: 20px;
      }
      .tip{
        display: flex;
        color: #ddd;
        margin: 20px 0;
        small, span{
          flex: 0 0 50%;
          width: 50%;
        }
        span{
          text-align: center;
        }
      }
      .btn {
        background: linear-gradient(45deg, #26c2be, #3486b5, #146897);
        background: -moz-linear-gradient(45deg, #26c2be, #3486b5, #146897);
        background: -ms-linear-gradient(45deg, #26c2be, #3486b5, #146897);
        background: -webkit-linear-gradient(45deg, #26c2be, #3486b5, #146897);
        margin-top: 20px;
        display: inline-block;
        color: #fff;
        padding: 5px 15px;
        border-radius: 6px;
      }
    }
    .confirm-box-width {
      margin: 0 15px;
      height: 300px;
      width: auto;
      overflow-y: auto;
      padding: 20px 15px;
      .tip{
        font-size: 14px;
      }
    }
    .confirm-box-width::-webkit-scrollbar {
      display: none;
    }
    .resuit-content {
      font-size: 14px;
    }
  }
</style>



