<template>
  <div class="st">
    <div class="choose-map">
      <div class="province" @click="showPro">
        <p>{{proName}}</p>
        <ul :class="showFlag ? 'active' : 'disnone'">
          <li v-for="item in proList" @click.stop="choosePro(item.address_name, item.address_id)" :data-id="item.address_id">
            {{item.address_name}}
          </li>
        </ul>
      </div>
      <div class="city" @click="showCity">
        <p>{{cityName}}</p>
        <ul :class="showFlag1 ? 'active' : 'disnone'">
          <li v-for="item in cityList" @click.stop="chooseCity(item.address_name, item.address_id)" :data-id="item.address_id">
            {{item.address_name}}
          </li>
        </ul>
      </div>
      <div class="county" @click="showCounty">
        <p>{{countyName}}</p>
        <ul :class="showFlag2 ? 'active' : 'disnone'">
          <li v-for="item in countyList" @click.stop="chooseCounty(item.address_name, item.address_id)" :data-id="item.address_id">
            {{item.address_name}}
          </li>
        </ul>
      </div>
    </div>
    <div id="container"></div>
    <div class="msg msg-pc" v-if="msg.length && state == 0" ref="msg">
      {{msg}}<span @click="goTo">点击加盟</span>
    </div>
    <div class="msg msg-wap" v-if="msg.length && state == 0" ref="msg">
      {{msg}}<span @click="goTo">点击加盟</span>
    </div>
    <div class="msg" v-else-if="msg.length && state == 1" ref="msg">
      <h3>{{msg}}</h3>
      <p>地址：{{address}}</p>
      <p>联系人：{{contacts}}</p>
      <p>电话：{{tel}}</p>
    </div>
  </div>
</template>

<script>
  import {axiosGet, axiosPost} from "../../api/config"

  export default {
    data() {
      return {
        proList: [],
        proName: '请选择省',
        cityList: [],
        cityName: '请选择市',
        countyList: [],
        countyName: '请选择县',
        showFlag: false,
        showFlag1: false,
        showFlag2: false,
        addressName: '',
        msg: '',
        state: 0,
        address:'',
        contacts: '',
        tel:''
      }
    },
    created() {
      axiosGet('/address/pro').then(res => {
        if(res.status === 200) {
          this.proList = res.data
        }
      })
    },
    mounted() {
      this.$nextTick(() => {
        this.loadMap()
      })
    },
    methods: {
      loadMap() {
        let _this = this
        this.map = new AMap.Map('container', {
          center: _this.center,
          zoom: 16
        })
      },
      geocoder(address, state, lng, lat) {
        let _this = this
        let lng1 = 0
        let lat1 = 0
        let tempHg = 0
        if(lng && lat) {
          _this.map.setZoomAndCenter(16,[lng,lat])

          this.$nextTick(() => {
            tempHg = _this.$refs.msg.offsetHeight
            _this.$refs.msg.style.marginTop = `-${tempHg}px`
          })
          return
        }
        let geocoderFunc = new AMap.Geocoder({
          radius: 1000 //范围，默认：500
        });
        geocoderFunc.getLocation(address, function(status, result) {
          if (status === 'complete' && result.info === 'OK') {
            lng1 = result.geocodes[0].location.lng
            lat1 = result.geocodes[0].location.lat
            _this.map.setZoomAndCenter(16, [lng1, lat1])
            _this.map.setFitView()
            if(!state){
              tempHg = _this.$refs.msg.offsetHeight
              _this.$refs.msg.style.marginTop = `-${tempHg+30}px`
            }
            tempHg = _this.$refs.msg.offsetHeight
            _this.$refs.msg.style.marginTop = `-${tempHg}px`
          }
        });
      },
      choosePro(name, id) {
        this.showFlag = false
        this.proName = name
        this.cityName = '请选择市'
        this.countyName = '请选择县'
        this.cityList = []
        this.countyList = []
        axiosGet(`/addresss/${id}`).then(res => {
          if(res.status === 200) {
            this.cityList = res.data
          }
        })
      },
      chooseCity(name, id) {
        this.showFlag1 = false
        this.cityName = name
        this.countyName = '请选择县'
        this.countyList = []
        axiosGet(`/addresss/${id}`).then(res => {
          if(res.status === 200) {
            this.countyList = res.data
          }
        })
      },
      chooseCounty(name, id) {
        this.showFlag2 = false
        this.countyName = name
        this.addressName = `${this.proName}${this.cityName}${this.countyName}`
        axiosPost('/adrcheck', {params: {adrcode: id}}).then(res => {
          if(res.status === 200) {
            if(res.data.state == 0) {
              this.msg = res.data.msg
              this.state = res.data.state
              this.geocoder(this.addressName, this.state)

            } else {
              let {address, msg, contacts, tel, state, xpoint, ypoint} = res.data
              this.msg = msg
              this.address = address
              this.contacts = contacts
              this.tel = tel
              this.state = state
              this.geocoder(this.address,state, xpoint, ypoint)
            }
          }
        })
      },
      showPro() {
        this.showFlag = !this.showFlag
        this.showFlag1 = false
        this.showFlag2 = false
      },
      showCity() {
        this.showFlag1 = !this.showFlag1
        this.showFlag2 = false
      },
      showCounty() {
        this.showFlag2 = !this.showFlag2
      },
      goTo() {
        this.$router.push({
          path: '/join'
        })
      }
    }
  }
</script>

